.GetIgnorecontainer {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top:20px;
}
.IgnoreCheckcontainer{
width: 100%;
justify-content: center;
align-items: center;
text-align: center;
font-family: "Roboto", Helvetica;
font-size: 15px;
}
.label {
  margin-bottom: 10px;
}

.input-field {
  width: 400px;
  height: 30px;
  padding: 5px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
}

.ignorecontainer{
  display : flex;
  width: 100%;
}

.ignorebox1 {
width: 70%;
float: left;
background-color: #f3fafe ;
margin: 10px;
border-radius: 10px;
padding: 10px;
border: 1px dashed #D3D3D3;
}


.ignorebox2 {
width: 30%;
float: left;
background-color: #f3fafe ;
margin: 10px;
border-radius: 10px;
padding: 10px;
border: 1px dashed #D3D3D3;
}

.ignoretext{
font: 15px "Roboto", Helvetica;
height: 100%;
}
.ignoretext tbody{
width: 50px;
}

.ignoretext ul {
  list-style-type: none;
  margin: 0;
  padding: 5;
}
.ignoretable {
  width: 100%;
  max-width: 100%;
}

.ignoretable th{
text-align: left;
}
.ignoretext table tr td{
padding-left: 10px;
}

.pathsearchrow{
display: flex;
width: 65%;
border-radius: 30px;
border: 1px solid #D3D3D3;
align-items: center;
font-family: "Roboto", Helvetica;
}

.pathsearchbar{
border: none;
padding-left: 10px;
padding: 10px;
width: 100%;
outline: none;
font-size: 16px;
height:10px;
border-radius: 16px;
}


.pathsearchrow:focus-within{
box-shadow: 0 0 5px 0px #D3D3D3;
}

.pathsearchlabel{
margin: 10px;
width: 20%;
font-weight: bold;
}

.gitIgnorebutton{
background-color: #2455C5;
border: none;
color: white;
height: 40px;
border-radius: 20px;
font-size: 18px;
margin-left: 10px;
padding: 10px;
}
.filesdiff{
width: 100%;
}

.filesToIgnore{
width: 100%;
}

.recent-searches{
margin-top: 3px;
margin-bottom: 0 ;
padding-inline-start: 0;
}
.recent-search-bullets{
list-style-type: none;
display: inline-block;
}

.recent-search-button{
background-color:#f3fafe;
color: #3EB0F7;
border: dashed 1px #D3D3D3;
border-radius: 20px;
font-size: 13px;
margin:2px;

}

.ignoretips{
width: 50%;
background-color: #f3fafe ;
margin-left: 25%;
margin-top: 10px;
border-radius: 10px;
padding: 10px;
border: 1px dashed #D3D3D3;
font: 15px "Roboto", Helvetica;
}

.ignoretips h2{
text-align: center;
}

.ignoretextarea {
  width: 100%;
  font-family: Arial, sans-serif;
  font-size: 14px;
  border-radius: 7px;
  height: auto;
  border:none;
  line-height: 1.5;
  resize: vertical;
}

.ignoretextarea::placeholder {
  color: #888888;
}

.ignoretextarea:focus {
  outline: none;
  border-color: #7c7c7c;
  box-shadow: 0 0 5px #7c7c7c;
}



.saveIgnoreButton{
background-color: #2455C5;
border: none;
color: white;
height: 25px;
float: right;
border-radius: 6px;
font-size: 18px;
text-align: center;
padding: 2px;
margin: 2px;
}
